* {
  outline: none;
}
a {
  text-decoration: none;
  color: white;
}
img {
  width: 100%;
  height: auto;
}
body {
  min-width: 320px;
  padding: 0;
  margin: 0;
  font-family: HelveticaNeueCyr, Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
}
header {
  position: sticky;
  top: 0;
  left: 0;
  z-index: 9999;
}
.wrapper {
  width: 1136px;
  margin: 0 auto;
}
.wp-admin a {
  color: #0073aa;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.slide-btn {
  position: relative;
  box-sizing: border-box;
  display: block;
  width: 34px;
  height: 34px;
  margin-left: 20px;
  border: 1px solid #CACCCF;
  background-color: transparent;
  font-size: 0;
  cursor: pointer;
}
.slide-btn:hover {
  background-color: #186490;
}
.slide-btn:active {
  background-color: #0C4D73;
}
.slide-btn:disabled {
  border: 1px solid #E8E8E8;
}
.slide-btn--next::before {
  content: url('../../img/svg/arrow_left.svg');
  transform: rotateY(180deg);
  position: absolute;
  top: 9px;
  right: 9px;
}
.slide-btn--prev::before {
  content: url('../../img/svg/arrow_left.svg');
  position: absolute;
  top: 9px;
  right: 9px;
}
.link--blue:hover {
  color: #186490;
}
.link--blue:active {
  color: #0C4D73;
}
.mute {
  display: none;
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 999;
  background-color: rgba(21, 25, 29, 0.8);
}
.main-nav__item a.link--active {
  color: #186490;
}
.blue-button {
  position: relative;
  box-sizing: border-box;
  width: 211px;
  border: none;
  padding: 16px 43px;
  background-color: transparent;
  font-family: 'HelveticaNeueCyr', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.2px;
  color: #ffffff;
  background-color: #186490;
  cursor: pointer;
}
.blue-button::before {
  content: url(../../img/svg/arow_right.svg);
  position: absolute;
  display: none;
  top: 35%;
  right: 5%;
}
.blue-button:active {
  background-color: #0C4D73;
}
.blue-button:disabled {
  background-color: #E8E8E8;
}
.blue-button:hover {
  text-align: left;
}
.blue-button:hover::before {
  display: block;
}
.gray-input {
  box-sizing: border-box;
  font-family: 'HelveticaNeueCyr', Arial, sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  background-color: transparent;
  border: 1px solid #52575C;
  border-radius: 1px;
  color: #52575C;
  cursor: pointer;
}
.gray-input:hover {
  border: 1px solid #186490;
  color: #186490;
}
.gray-input:hover::placeholder {
  color: #186490;
}
.h3-title {
  margin: 0;
  font-weight: 900;
  font-size: 32px;
  line-height: 42px;
  letter-spacing: 0.1px;
  color: #15191D;
}
.h4-title {
  margin: 0;
  font-weight: 300;
  font-size: 26px;
  line-height: 32px;
  letter-spacing: 0.2px;
  color: #15191D;
}
.body-1 {
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
}
.body-2 {
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.1px;
}
.small-1 {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  letter-spacing: 0.2px;
}
.subtitle-1 {
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
}
#slider {
  position: relative;
}
.slider__wrapper {
  position: relative;
  max-width: 1136px;
  margin: 0 auto;
}
@media (max-width: 1140px) {
  header .wrapper {
    width: 100%;
    margin-right: 25px;
    margin-left: 25px;
  }
  .wrapper {
    box-sizing: border-box;
    width: 100%;
    padding-right: 25px;
    padding-left: 25px;
  }
  .h3-title.serch-result__title {
    margin-top: 35px;
    margin-bottom: 45px;
    width: 100%;
  }
}
@media (min-width: 320px) and (max-width: 575px) {
  .h3-title {
    font-size: 18px;
    line-height: 20px;
    letter-spacing: 0.1px;
  }
  .h4-title {
    font-size: 16px;
    line-height: 18px;
  }
  .subtitle-1 {
    font-size: 12px;
    line-height: 16px;
  }
  .body-1 {
    font-size: 12px;
    line-height: 18px;
  }
  .mobile-hidden {
    display: none;
  }
}

.list-normalize {
  padding: 0;
  margin: 0;
  list-style: none;
}

.top-contact {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #15191D;
  color: #A0A4A8;
  font-weight: normal;
  font-size: 11px;
  line-height: 14px;
  letter-spacing: 0.2px;
  border-bottom: 1px solid #2A2E32;
}
.top-contact__container {
  display: block;
}
.top-contact a {
  color: #A0A4A8;
}
.top-contact .wrapper {
  position: relative;
  width: 1284px;
  display: flex;
  justify-content: space-between;
}
.top-contact__social {
  position: relative;
  margin-right: 44px;
  text-transform: uppercase;
}
.top-contact__social-container {
  padding: 12px 0;
  position: relative;
  display: flex;
  z-index: 99999;
}
.top-contact__social-container:hover > .top-contact__dropDown {
  display: block;
}
.top-contact__dropDown {
  position: absolute;
  z-index: 3;
  top: 27px;
  right: -68px;
  display: none;
  padding: 0;
  margin: 0;
  background: #52575C;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 1px;
}
.top-contact__dropDown-item {
  list-style: none;
}
.top-contact__dropDown-item .top-contact__dropDown-link {
  display: block;
  padding-top: 7px;
  padding-bottom: 5px;
  padding-left: 16px;
  padding-right: 23px;
  font-size: 11px;
  line-height: 14px;
  letter-spacing: 0.2px;
  color: #F9F9FA;
}
.top-contact__dropDown-item .top-contact__dropDown-link:hover {
  background-color: #15191D;
}
.top-contacts__adres {
  margin-right: 11px;
  position: relative;
  align-self: center;
}
.top-contacts__adres::before {
  content: url('../../img/svg/location.svg');
  position: absolute;
  top: 0px;
  left: -15px;
}
.top-contact__phone {
  position: relative;
  align-self: center;
}
.top-contact__phone::before {
  content: url('../../img/svg/phone.svg');
  position: absolute;
  top: 0;
  left: -15px;
}
.top-contact__working-hours {
  position: relative;
  align-self: center;
}
.top-contact__working-hours::before {
  content: url(../../img/svg/clock.svg);
  position: absolute;
  top: 0;
  left: -13px;
}
a.visually-impaired {
  color: #F9F9FA;
  text-transform: uppercase;
  align-self: center;
}
@media (max-width: 1140px) {
  .top-contact__working-hours {
    display: none;
  }
  .top-contact__social {
    display: none;
  }
  .visually-impaired {
    display: none;
  }
  .top-contacts__adres {
    font-size: 8px;
  }
  .top-contact__phone {
    font-size: 8px;
  }
  .top-contact__index {
    display: none;
  }
  .top-contact .wrapper {
    padding: 12px 0;
  }
  .top-contact__social-container {
    display: none;
  }
}

.main-nav {
  display: flex;
}
.main-nav__list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  text-align: center;
  padding: 0;
  margin: 0;
  list-style: none;
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.1px;
}
.main-nav__item {
  box-sizing: border-box;
  padding: 22px 13px;
}
.main-nav__item--lk {
  display: none;
}
.main-nav__item a {
  font-family: HelveticaNeueCyr;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  letter-spacing: 0.1px;
  color: #E8E8E8;
  cursor: pointer;
}
.main-nav__burger-container {
  cursor: pointer;
  align-self: center;
  display: none;
}
@media (max-width: 1140px) {
  .main-nav__list {
    flex-direction: column;
  }
  .main-nav__item {
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .main-nav__item--lk {
    display: block;
  }
  .main-nav__burger-container {
    display: block;
  }
}

#slider {
  max-height: 500px;
}
.first-screen {
  position: relative;
  max-height: 500px;
}
.gray {
  position: absolute;
  width: 100%;
  height: 100%;
  background: #15191D;
  opacity: 0.8;
  z-index: 1;
}
.slider__text-content {
  position: relative;
  width: 630px;
  padding-bottom: 90px;
  z-index: 2;
}
.first-screen__slider-img {
  position: absolute;
  top: 0;
  left: 0;
}
.slider__title {
  margin: 0;
  font-size: 52px;
  font-weight: 300;
  line-height: 54px;
  letter-spacing: 0.2px;
  color: #FFFFFF;
}
.slider__description {
  margin-top: 17px;
  margin-bottom: 40px;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.1px;
  color: #CACCCF;
  width: 480px;
}
.slider__text {
  display: flex;
  padding: 16px 0;
  justify-content: center;
  transition: all 0.25s;
}
.slider__button {
  position: relative;
  overflow: hidden;
  display: block;
  padding: 0 5px;
  width: 200px;
  background: #186490;
  border-radius: 1px;
  text-align: center;
  letter-spacing: 0.2px;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 18px;
  cursor: pointer;
}
.slider__button::after {
  content: url(../../img/svg/arow_right.svg);
  position: absolute;
  top: 17px;
  right: -17px;
}
.slider__button:active {
  background-color: #0C4D73;
}
.slider__button:hover > .slider__text {
  display: inline-block;
  transform: translateX(-50px);
  width: 100%;
}
.slider__button:hover:after {
  transition: all 0.25s;
  transform: translateX(-30px);
}
.slider__item {
  position: relative;
  background-image: url(../../img/Operativka_1.jpg);
  background-size: cover;
  height: 500px;
}
#slider .slick-dots {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 16px;
  z-index: 2;
}
#slider .slick-dots li {
  padding: 0;
  width: 4px;
  height: 4px;
}
#slider .slick-dots li button:before {
  content: '';
  width: 4px;
  height: 4px;
  background-color: #FFFFFF;
}
#slider .slick-next {
  right: 0;
  width: 200px;
  height: 500px;
}
#slider .slick-next:hover {
  background: linear-gradient(270deg, #15191D 0%, rgba(21, 25, 29, 0) 100%);
  opacity: 0.8;
}
#slider .slick-next::before {
  content: url('../../img/svg/slider-arrow.svg');
  position: absolute;
  top: 45%;
  left: 50%;
  width: 30px;
  height: 30px;
  opacity: 0.6;
}
#slider .slick-next:hover::before {
  opacity: 1;
}
#slider .slick-slide {
  display: flex;
  height: 500px;
}
.slick-slide .wrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.progressBarContainer {
  position: absolute;
  top: 8px;
  left: -30px;
}
.item {
  width: 2px;
  height: 90px;
  background-color: rgba(202, 204, 207, 0.4);
}
.progressBar {
  display: flex;
  width: 100%;
  height: 1%;
  background-color: #fff;
}
@media (min-width: 320px) and (max-width: 575px) {
  .progressBarContainer {
    display: none;
  }
  .slider__text-content {
    width: 270px;
    padding-bottom: 40px;
    padding-top: 20px;
  }
  .slider__title {
    width: 270px;
    margin-bottom: 20px;
    font-weight: 300;
    font-size: 20px;
    line-height: 22px;
    letter-spacing: 0.2px;
  }
  .slider__description {
    display: none;
  }
  .slider__button {
    width: 98%;
  }
  #slider .slick-slide {
    height: 370px;
  }
  #slider .slick-next {
    position: absolute;
    width: 0px;
    height: 0px;
  }
  #slider .slick-next::before {
    display: none;
  }
}

